<template>
    <main>
        <div class="main">
            <div class="user-wrapp">
                <div v-if="found" class="user-profile">
                    <img :src="user.avatar_url" alt="">
                    <div class="username">
                        <h2>{{ user.login }}</h2>
                        <span>Membro desde {{ formatDate(user.created_at) }}</span>
                    </div>
                    <div class="user-caption">
                        {{ user.bio }}
                    </div>
                </div>
                <div class="user-info">
                    <ul>
                        <li><a class="tab-links" @click="changeTab('1')">Repositórios</a></li>
                        <li><a class="tab-links" @click="changeTab('2')">Gists</a></li>
                        <li><a class="tab-links" @click="changeTab('3')">Seguidores</a></li>
                        <li><a class="tab-links" @click="changeTab('4')">Seguindo</a></li>
                    </ul>
                    <div v-if="choice == 1" class="user-repositories">
                        <a target="_blank" class="repos-links" v-for="repo in repositories" :key="repo.id"
                            :href="repo.html_url">
                            <div class="user-repo">
                                <h2>{{ repo.name }}</h2>
                                <h6>Atualizado em {{ formatDate(repo.updated_at) }}</h6>
                                <p>{{ repo.description }}</p>
                            </div>
                        </a>
                    </div>
                    <div v-if="choice == 2" class="user-repositories">
                        <a target="_blank" class="repos-links" v-for="gist in gists" :key="gist.id"
                            :href="gist.html_url">
                            <div class="user-repo">
                                <h2>{{ gist.name }}</h2>
                                <h6>Atualizado em {{ formatDate(gist.created_at) }}</h6>
                                <p>{{ gist.description }}</p>
                            </div>
                        </a>
                    </div>
                    <div v-if="choice == 4" class="user-following">

                        <div class="card fade-in" v-for="flw in following" :key="flw.id">
                            <a target="_blank" class="repos-links" :href="flw.html_url">
                                <div class="img"><img :src="flw.avatar_url" alt="avatar">
                                </div>
                                <h4><b>{{ flw.login }}</b></h4>
                            </a>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </main>
</template>

<script>
export default {
    name: "User",
    data() {
        return {
            option: "Você selecionou Repositórios",
            choice: 0,
            results: [],
            repositories: [],
            following: [],
            gists: [],
            endpoint: "",
            user: null,
            found: false,
            response: null
        }
    },
    props: {
        username: { type: String, required: true }
    },
    methods: {
        async getData(endpoint = "") {
            await this.$axios.get(`https://api.github.com/users/${this.username}${endpoint}`)
                .then((response) => {
                    this.response = response.data;
                    this.processResponse(this.response);
                })
                .catch(function (error) {
                    console.error(error);
                });
        },
        processResponse(response) {
            if (!this.user) {
                this.user = response;
                this.found = true;
            } else if (this.endpoint == "/repos") {
                this.repositories = response;
            } else if (this.endpoint == "/gists") {
                if (response.length) {
                    for (let i = 0; i < response.length; i++) {
                        let f = null;
                        for (let file in response[i].files) {
                            f = file;
                        }
                        this.gists[i] = { name: f, created_at: response[i].created_at, description: response[i].description, html_url: response[i].html_url }
                    }
                } else {
                    console.log("Nenhum resultado obtido!");
                }
            } else if (this.endpoint == "/following") {
                this.following = response;
            }
        },
        formatDate(date) {
            return date.replace(/(\d{4})-(\d{2})-(\d{2}).*/, "$3/$2/$1");
        },
        changeTab(value) {
            switch (value) {
                case '1':
                    this.choice = 1;
                    this.endpoint = "/repos";
                    (!this.repositories.length ? this.getData(this.endpoint) : "");
                    break;
                case '2':
                    this.choice = 2
                    this.endpoint = "/gists";
                    (!this.gists.length ? this.getData(this.endpoint) : "");
                    break;
                case '3':
                    this.choice = 3
                    break;
                case '4':
                    this.choice = 4
                    this.endpoint = "/following";
                    (!this.following.length ? this.getData(this.endpoint) : "");
                    break;
            }
        },
        getUserGists() {
            this.endpoint = "/gists";

        }

    },
    created() {
        this.getData();
    }
}
</script>

<style>
.user-wrapp {
    max-width: calc(1300px - 40px);
    padding: 0 20px;
    margin: 20px auto;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.user-profile {
    flex-basis: 31%;
    position: relative;
    padding: 0 10px;
    height: 20%;
}

.user-profile img {
    width: 100%;
}

.user-info {
    flex-basis: 55%;
    padding: 0 10px;
    margin-top: 10px;
}

.user-info .user-repositories {
    background-color: #ffffff;
    border: 1px solid #dddddd;
    margin-top: 10px;
    border-radius: 2px;
}

.user-following {
    display: flex;
    background-color: #ffffff;
    border: 1px solid #dddddd;
    margin-top: 10px;
    border-radius: 2px;
    flex-wrap: wrap;
    justify-content: space-around;
}

.user-following img {
    width: 100%;
    display: block;
}

.user-repo {
    border-bottom: 1px solid #dddddd;
    padding: 10px 20px;
}

.user-repo:hover {
    background: #ccc;
}

.user-repo h2,
.user-repo h6,
.user-repo p {
    margin-bottom: 10px;
}

.user-repo p {
    font-size: 1em;
}

.user-repo h6 {
    font-size: 0.875em;
    font-weight: 300;
}

.user-repo h2 {
    margin-top: 10px;
    font-weight: 500;
    font-size: 1.2em;
}

.user-info ul {
    display: flex;
    align-items: stretch;
    border: 1px solid #dddddd;
}

.user-info a {
    color: #777777;
    background-color: #ffffff;
    border-bottom: 1px solid #dddddd;
    cursor: pointer;
    padding: 10px 20px;
}

.repos-links {
    text-decoration: none;
    border: none !important;
    padding: 0 !important;
}

.username {
    position: absolute;
    bottom: 70px;
    margin: 0 20px;
    color: #fff;
}

.user-caption {
    padding: 0 20px;
}

.username>h2 {
    font-size: 27px;
    font-weight: lighter;
    position: relative;
    margin: 31px 0 4px;
    text-transform: uppercase;
    text-shadow: 1px 1px 2px #000;
}

@media (max-width: 30em) {
    .user-profile {
        flex-basis: 100%;
        padding: 0 10px;
    }

    .main-header-content h1 a {
        font-size: 0.75em;
    }

    .tab-links {
        display: block;
        padding: 20px 20px !important;
        text-align: center;
        background-color: #2980b9 !important;
        border-bottom: 1px solid #fff !important;
        color: #FFF !important;
        font-weight: 500;
    }

    .user-info ul {
        display: block;
    }

    .user-info {
        flex-basis: 100%;
    }
}
</style>